import { writable } from "svelte/store";
export default function drag(node, parameters) {
    const currentOffset = { x: 0, y: 0 };
    let unsub = parameters.offset.subscribe(handleOffsetChange);
    function handleOffsetChange(val) {
        // copy the values
        currentOffset.x = val.x;
        currentOffset.y = val.y;
    }
    function handlePointerDown(e) {
        if ((typeof parameters.button === "number" && e.button !== parameters.button) ||
            (Array.isArray(parameters.button) && !parameters.button.includes(e.button))) {
            return;
        }
        e.stopImmediatePropagation();
        e.preventDefault();
        this.dispatchEvent(new CustomEvent("dragstart"));
        const pointerStart = { x: e.clientX, y: e.clientY };
        const startOffset = Object.assign({}, currentOffset);
        function handlePointerMove(e) {
            const delta = {
                x: e.clientX - pointerStart.x,
                y: e.clientY - pointerStart.y,
            };
            const factor = parameters.invert ? -1 : 1;
            parameters.offset.set({
                x: startOffset.x + delta.x * factor,
                y: startOffset.y + delta.y * factor,
            });
        }
        function handlePointerUp(e) {
            this.removeEventListener("pointermove", handlePointerMove);
            this.removeEventListener("pointerup", handlePointerUp);
            this.releasePointerCapture(e.pointerId);
            this.dispatchEvent(new CustomEvent("dragend"));
        }
        this.addEventListener("pointermove", handlePointerMove);
        this.addEventListener("pointerup", handlePointerUp);
        this.setPointerCapture(e.pointerId);
    }
    node.addEventListener("pointerdown", handlePointerDown);
    return {
        update(newOptions) {
            parameters = newOptions;
            unsub();
            unsub = parameters.offset.subscribe(handleOffsetChange);
        },
        destroy() {
            unsub();
        },
    };
}
export function dragscroller(node, button = 1) {
    const scrollStore = writable({ x: node.scrollLeft, y: node.scrollTop });
    const scroll = {
        subscribe: scrollStore.subscribe,
        set(offset) {
            node.scrollLeft = offset.x;
            node.scrollTop = offset.y;
            scrollStore.set({
                x: node.scrollLeft,
                y: node.scrollTop,
            });
        },
    };
    const action = drag(node, {
        button,
        offset: scroll,
    });
    return {
        update(newButton) {
            button = newButton;
            action.update({
                button,
                offset: scroll,
            });
        },
        destroy: action.destroy,
    };
}
